.navigation {
    font-size: 1.7rem;
   
   // background: transparent;
  // background: red;
    margin: .0 4rem 0 2rem;
    display: flex;
    justify-content: space-between;
z-index: 999999;
position: relative;


}

//navigation logo container
.nav-logo {
    display: flex;
   align-items: center;
    justify-content: flex-start;

    
}

.nav-logo-item {
   // width: 5.5rem;
    height: 4rem;
    background: transparent;
    text-decoration: none;

    color: $white;
    font-size: 2rem;
    padding: 0 .5rem;
    border-radius: 100%;
    border:3px solid $white; 
   
}

//social media icons
.social-media {
    display: flex;
    align-items: center;
    font-size: 2.5rem;
    // justify-content: flex-end;
    // padding: 0 1rem;
}

//main menu 
.main-menu {
    display: flex;
    align-items: center;
}

